<template>
  <div>
    <h3 class="font-weight-bold">Promotion QR Code (Demo)</h3>

    <!-- input part -->
    <div>
      <label for="template_upload" class="font-weight-bold mr-2">匯入template:</label>
      <input id="template_upload" type="file" name="template_upload" accept="image/*" @change="readTemplateFile" ref="template_upload" />
    </div>
    <div>
      <label for="fileupload" class="font-weight-bold mr-2">匯入CSV:</label>
      <input id="fileupload" type="file" name="csv_upload" accept=".csv,.txt" @change="readCsvFile" ref="csv_upload" />
    </div>

    <div>
      <a class="m-1" href="test_background.png" download="test_background.png">Template Sample</a>
      <a class="m-1" href="test_promo_code.txt" download="test_promo_code.txt">CSV Sample</a>
    </div>
    <div>
      <div>
        <label for="paste_dx" class="mr-1">貼上位置X：</label>
        <input type="number" id="paste_dx" v-model="paste_dx" min="0">
      </div>
      <div>
        <label for="paste_dy" class="mr-1">貼上位置Y：</label>
        <input type="number" id="paste_dy" v-model="paste_dy" min="0">
      </div>
      <div>
        <label for="qr_size" class="mr-1">二維碼大小：</label>
        <input type="number" id="qr_size" v-model="qr_size" min="10">
      </div>
    </div>

    <!-- debug -->
    <div>
      <div>
        <!-- <button type="button" class="btn btn-primary" @click="debug">Debug</button> -->
        <button type="button" class="btn btn-danger m-1" @click="clear_data">清除資料</button>
        <button type="button" class="btn btn-primary m-1" @click="generateQrcode" v-if="csv_content">生成二維碼</button>    
        <!-- <button type="button" class="btn btn-primary" @click="draw_canvas">Draw canvas</button> -->
        <button type="button" class="btn btn-info m-1 d-none" @click="paste_qrcode_to_canvas_all" v-if="isShowActionButton">貼上全部二維碼</button>
      </div>
      <div>
        <!-- <button type="button" class="btn btn-primary" @click="download_qrcode_all" v-if="isShowActionButton">下載全部二維碼</button>
        <button type="button" class="btn btn-primary" @click="download_canvas_all" v-if="isShowActionButton">下載全部合成圖片</button> -->
        <button type="button" class="btn btn-success m-1" @click="download_qrcode_all_zip" v-if="isShowActionButton">下載全部二維碼(Zip)</button>
        <button type="button" class="btn btn-success m-1" @click="download_canvas_all_zip" v-if="isShowActionButton">下載全部合成圖片(Zip)</button>
      </div>
      
      <div v-if="isShowActionButton">
        已貼上 {{ pasted_qrcode_qty }} / {{ promo_codes.length }} 個 QR Code。請待全部QR Code貼上後才下載合成圖片。
      </div>
    </div>


    <!-- result  -->
    <div v-if="isShowResult">
      <table class="table">
        <thead class="thead-dark">
          <tr>
            <th>Promotion Code</th>
            <th>QR Code</th>
            <th>合成圖片</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="promo_code in promo_codes" :key="promo_code.id">
            <td>{{ promo_code }}</td>
            <td>
              <QrcodeVue :value="promo_code" :id="'qrcode_'+promo_code" :size="qr_size" class="qrcode" />
              <div>
                <button type="button" class="btn btn-success m-1" @click="download_qrcode(promo_code)">
                  <a href="" :download="promo_code + '.png'" class="download-button d-none" :id="'a_qrcode_' + promo_code">下載</a>
                  <span>下載</span>
                </button>
                <button type="button" class="btn btn-primary m-1" @click="copy_qrcode_to_clipboard(promo_code)">複製至剪貼簿</button>
                <button type="button" class="btn btn-info paste-qrcode-button d-none" @click="paste_qrcode_to_canvas(promo_code)">貼上Template</button>
              </div>
            </td>
            <td>
              <canvas :id="'canvas_promo_code_' + promo_code" class="canvas_result"></canvas>
              <div>
                <button type="button" class="btn btn-primary m-1" @click="download_canvas(promo_code)">
                  <a href="" :download="promo_code + '.png'" class="download-canvas-button d-none" :id="'a_canvas_' + promo_code">下載圖片</a>
                  <span>下載圖片</span>
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>
<script>
import QrcodeVue from 'qrcode.vue'
import JSZip from 'jszip'
import { saveAs } from 'file-saver';

export default {
  components: {
    QrcodeVue,
  },
  props: {

  },
  data() {
    return {
      isShowResult: false,
      template_content: "",
      csv_content: "",
      promo_codes: [],
      canvas_width: "800",
      canvas_height: "600",

      paste_dx: "650",
      paste_dy: "50",
      qr_size: 100,

      pasted_qrcode_qty: 0,
    }
  },
  computed: {
    isShowActionButton() {
      return this.promo_codes.length > 0 && this.isShowResult;
    },
  },
  created() {

  },
  mounted() {

  },
  methods: {
    debug() {
      console.log("template_content");
      console.log(this.template_content);
      console.log("csv_content");
      console.log(this.csv_content);
      console.log("promo_codes");
      console.log(this.promo_codes);
      console.log("paste_dx");
      console.log(this.paste_dx);
      console.log("paste_dy");
      console.log(this.paste_dy);
      console.log("canvas_width");
      console.log(this.canvas_width);
      console.log("canvas_height");
      console.log(this.canvas_height);
    },
    clear_data() {
      this.$refs.template_upload.value = null;
      this.$refs.csv_upload.value = null;
      this.csv_content = "";
      this.template_content = "";
      this.promo_codes = [];
    },
    draw_canvas() {
      let self = this;
      let all_canvas = document.getElementsByClassName('canvas_result');
      let img_uri = this.template_content;
      let img = new Image();
      img.src = img_uri; 
      img.onload = () => {
        for (let canvas of all_canvas) {
          let ctx = canvas.getContext("2d");
          ctx.canvas.width = self.canvas_width;
          ctx.canvas.height = self.canvas_height;
          ctx.canvas.width = img.width;
          ctx.canvas.height = img.height;
          ctx.drawImage(img, 0, 0, self.canvas_width, self.canvas_height);
        }
        this.paste_qrcode_to_canvas_all();
      };
    },
    readTemplateFile(event) { // actually this is template in image format
      this.isShowResult = false;
      let self = this;
      var fr = new FileReader();
      fr.onload = function(){
        self.template_content = fr.result;
        let img = new Image();
        img.src = fr.result;

        img.onload = () => {
          if (img.width) {
            self.canvas_width = img.width;
          }
          if (img.height) {
            self.canvas_height = img.height;
          }
        };
        
      } 
      try {
        if (event.target.files[0]) {
          fr.readAsDataURL(event.target.files[0]);
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    readCsvFile(event) {
      this.isShowResult = false;
      let self = this;
      var fr = new FileReader();
      fr.onload = function(){
        // self.canGenerate = false;

        self.csv_content = fr.result;
        self.parseCsvFile();
      } 
      try {
        if (event.target.files[0]) {
          fr.readAsText(event.target.files[0]);
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    parseCsvFile() {
      this.promo_codes = this.csv_content.split('\n').map((str) => str.trim()).filter(str => str);
    },
    generateQrcode() {
      this.isShowResult = true;
      this.draw_canvas();
    },

    // get image data
    get_uri_from_promo_code(promo_code) {
      let qrcode_canvas = document.getElementById('qrcode_'+promo_code).childNodes[0];
      let dataUri = qrcode_canvas.toDataURL("image/png");
      return dataUri;
    },

    download_qrcode(promo_code) {
      let a_qrcode = document.getElementById('a_qrcode_'+promo_code);
      let dataUri = this.get_uri_from_promo_code(promo_code);
      a_qrcode.href = dataUri;
      a_qrcode.click();
    },

    copy_qrcode_to_clipboard(promo_code) {
      let dataUri = this.get_uri_from_promo_code(promo_code);
      let blob_data = fetch(dataUri).then(res => res.blob());

      try {
        navigator.clipboard.write([
          new ClipboardItem({
            'image/png': blob_data
          })
          ]);
      } catch (error) {
          console.error(error);
          alert("無法複製!");
      }
    },

    paste_qrcode_to_canvas(promo_code) {
      let canvas = document.getElementById('canvas_promo_code_' + promo_code);
      let img_uri = this.get_uri_from_promo_code(promo_code);
      let img = new Image();
      img.src = img_uri;
      img.onload = () => {
        let ctx = canvas.getContext("2d");
        ctx.drawImage(img, this.paste_dx, this.paste_dy, this.qr_size, this.qr_size);
      }; 
    },
    
    paste_qrcode_to_canvas_all() {
      this.pasted_qrcode_qty = 0;
      let buttons = document.getElementsByClassName('paste-qrcode-button');

      let i = 0;      
       // eslint-disable-next-line  no-unused-vars
      let a = setInterval(() => {
        buttons[i].click();
        i++;
        this.pasted_qrcode_qty = i;
        if (i >= buttons.length) {
          clearInterval(a);
          console.log("clear interval");
        }
      }, 10);
    },

    // get image data
    get_uri_from_canvas(promo_code) {
      let canvas = document.getElementById('canvas_promo_code_' + promo_code);
      let dataUri = canvas.toDataURL("image/png");
      return dataUri;
    },

    download_canvas(promo_code) {
      let a_canvas = document.getElementById('a_canvas_'+promo_code);
      let dataUri = this.get_uri_from_canvas(promo_code);
      a_canvas.href = dataUri;
      a_canvas.click();
    },
    
    download_qrcode_all() {
      let buttons = document.getElementsByClassName('download-button');

      let i = 0;      
       // eslint-disable-next-line  no-unused-vars
      let a = setInterval(() => {
        buttons[i].click();
        i++;
        if (i >= buttons.length) {
          clearInterval(a);
          console.log("clear interval download_qrcode_all");
        }
      }, 150);
    },
    
    download_canvas_all() {
      let buttons = document.getElementsByClassName('download-canvas-button');

      let i = 0;      
      // eslint-disable-next-line  no-unused-vars
      let a = setInterval(() => {
        buttons[i].click();
        i++;
        if (i >= buttons.length) {
          clearInterval(a);
          console.log("clear interval download_canvas_all");
        }
      }, 150);
    },

    download_qrcode_all_zip() {
      let qrcode_all = document.getElementsByClassName('qrcode');
      const zip = new JSZip();
      for (let i = 0; i < qrcode_all.length; i++) {
        let qrcode = qrcode_all[i];
        let canvas = qrcode_all[i].childNodes[0];
        let filename = qrcode.id.replace('qrcode_', '') + '.png';
        let imgData = canvas.toDataURL("image/png").replace('data:image/png;base64,', '');
        
        zip.file(filename, imgData, {base64: true});
      }
      zip.generateAsync({type:"blob"}).then(function(content) {
        // see FileSaver.js
        saveAs(content, "Promotion_Code.zip");
      });
    },
    download_canvas_all_zip() {
      console.log("download_canvas_all_zip");
      let canvas_all = document.getElementsByClassName('canvas_result');
      const zip = new JSZip();
      for (let i = 0; i < canvas_all.length; i++) {
        let canvas = canvas_all[i];
        let filename = canvas.id.replace('canvas_promo_code_', '') + '.png';
        let imgData = canvas.toDataURL("image/png").replace('data:image/png;base64,', '');
        
        zip.file(filename, imgData, {base64: true});
      }
      zip.generateAsync({type:"blob"}).then(function(content) {
        // see FileSaver.js
        saveAs(content, "Promotion_Code.zip");
      });
    },
  },
}
</script>
<style scoped>

</style>